import { factories } from "@/lib/factories";
import Link from "next/link";
import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

const regions = ["전체", ...Array.from(new Set(factories.map(f => f.region)))];
const allItems = Array.from(new Set(factories.flatMap(f => f.items)));

export default function FactoriesPage() {
  const [region, setRegion] = useState("전체");
  const [item, setItem] = useState("");
  const [search, setSearch] = useState("");

  const filtered = factories.filter(f => {
    return (
      (region === "전체" || f.region === region) &&
      (!item || f.items.includes(item)) &&
      (f.name.includes(search) || f.description.includes(search))
    );
  });

  return (
    <div className="max-w-4xl mx-auto py-8">
      <h1 className="text-2xl font-bold text-toss-blue mb-6">봉제공장 찾기</h1>
      <div className="flex flex-wrap gap-2 mb-6">
        <select value={region} onChange={e => setRegion(e.target.value)} className="border rounded px-3 py-2">
          {regions.map(r => <option key={r}>{r}</option>)}
        </select>
        <select value={item} onChange={e => setItem(e.target.value)} className="border rounded px-3 py-2">
          <option value="">전체 품목</option>
          {allItems.map(i => <option key={i}>{i}</option>)}
        </select>
        <input
          value={search}
          onChange={e => setSearch(e.target.value)}
          placeholder="공장명/설명 검색"
          className="border rounded px-3 py-2 flex-1 min-w-[180px]"
        />
      </div>
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
        {filtered.length === 0 && (
          <div className="col-span-2 text-center text-gray-400">검색 결과가 없습니다.</div>
        )}
        {filtered.map(factory => (
          <Link key={factory.id} href={`/factories/${factory.id}`} className="group">
            <Card className="rounded-xl shadow-sm border bg-white hover:shadow-md transition-shadow cursor-pointer h-full flex flex-col">
              <CardHeader className="pb-2">
                <CardTitle className="text-lg font-bold group-hover:text-toss-blue transition-colors">{factory.name}</CardTitle>
              </CardHeader>
              <CardContent className="flex-1 flex flex-col">
                <img src={factory.image} alt={factory.name} className="rounded-xl mb-3 h-36 w-full object-cover" />
                <div className="text-sm text-gray-500 mb-2 flex-1">{factory.description}</div>
                <div className="flex flex-wrap gap-2 text-xs mb-2">
                  <span className="bg-toss-gray rounded px-2 py-1">{factory.region}</span>
                  {factory.items.map(i => <span key={i} className="bg-toss-gray rounded px-2 py-1">{i}</span>)}
                  <span className="bg-toss-gray rounded px-2 py-1">최소 {factory.minOrder}장</span>
                </div>
                <Button asChild className="w-full mt-2 bg-toss-blue text-white rounded-full font-bold py-2 hover:bg-blue-600 transition-colors">
                  <span>상세보기</span>
                </Button>
              </CardContent>
            </Card>
          </Link>
        ))}
      </div>
    </div>
  );
} 